// src/App.tsx
import React, { useState, useMemo, useEffect } from 'react';
import { DashboardLayout } from './components/layout/DashboardLayout';
import { Header } from './components/layout/Header';
import { MetricCard } from './components/metrics/MetricCard';
import { LoadTimeChart } from './components/charts/LineChart';
import { PageViewsChart } from './components/charts/BarChart';
import { PerformanceScatterChart } from './components/charts/ScatterChart';
import { PerformanceTable } from './components/tables/PerformanceTable';
import AdvancedFilters from './components/filters/AdvancedFilters';
import { AuthModal } from './components/auth/AuthModal';
import { NotificationPanel } from './components/notifications/NotificationPanel';
import { NotificationToast } from './components/notifications/NotificationToast';
import { ThemeProvider } from './contexts/ThemeContext';
import { AuthProvider, useAuth } from './contexts/AuthContext';
import { NotificationProvider, useNotifications } from './contexts/NotificationContext';
import { useMetrics } from './hooks/useMetrics';
import { calculateAggregates } from './utils/calculations';
import type { MetricData } from './types';
// Simple exporter implemented inline to avoid missing module './utils/exporters'
const exportToCSV = (data: MetricData[], filename = 'export') => {
  try {
    if (!Array.isArray(data)) data = [data];
    const keys = Object.keys(data[0] || {}) as Array<keyof MetricData>;
    const rows = data.map(row =>
      keys.map(k => {
        const cell = row[k] ?? '';
        return `"${String(cell).replace(/"/g, '""')}"`;
      }).join(',')
    );
    const csv = [keys.join(','), ...rows].join('\n');
    const blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', `${filename}.csv`);
    document.body.appendChild(link);
    link.click();
    link.remove();
    URL.revokeObjectURL(url);
  } catch (e) {
    console.error('exportToCSV error', e);
  }
};

// (only CSV exporter used in this file)

import { Clock, TrendingUp, Users, DollarSign } from 'lucide-react';

// Composant principal wrapper avec tous les providers
const AppContent: React.FC = () => {
  const [timeframe, setTimeframe] = useState<'7d' | '30d' | '90d'>('7d');
  const [filters, setFilters] = useState({
    loadTime: { min: 0, max: 10 },
    bounceRate: { min: 0, max: 100 },
    pageViews: { min: 0, max: 1000000 },
    dateRange: { start: '', end: '' }
  });
  const [isFiltersOpen, setIsFiltersOpen] = useState(false);
  const [isAuthOpen, setIsAuthOpen] = useState(false);
  const [isNotificationsOpen, setIsNotificationsOpen] = useState(false);
  
  const { data, isLoading, error } = useMetrics(timeframe);
  const { isAuthenticated } = useAuth();
  const { notifications, removeNotification, addNotification } = useNotifications();

  // Notifications de démo
  useEffect(() => {
    if (isAuthenticated) {
      // Notification de bienvenue
      setTimeout(() => {
        addNotification({
          type: 'success',
          title: 'Bienvenue !',
          message: 'Vous êtes maintenant connecté à votre tableau de bord.',
        });
      }, 1000);

      // Notification de performance
      setTimeout(() => {
        addNotification({
          type: 'info',
          title: 'Performance stable',
          message: 'Vos métriques de performance restent dans les objectifs.',
        });
      }, 5000);
    }
  }, [isAuthenticated, addNotification]);

  // Appliquer les filtres aux données
  const filteredData = useMemo(() => {
    return data.filter(item => {
      const itemDate = new Date(item.timestamp);
      const meetsLoadTime = item.loadTime >= filters.loadTime.min && item.loadTime <= filters.loadTime.max;
      const meetsBounceRate = item.bounceRate >= filters.bounceRate.min && item.bounceRate <= filters.bounceRate.max;
      const meetsPageViews = item.pageViews >= filters.pageViews.min && item.pageViews <= filters.pageViews.max;
      
      let meetsDateRange = true;
      if (filters.dateRange.start) {
        meetsDateRange = meetsDateRange && itemDate >= new Date(filters.dateRange.start);
      }
      if (filters.dateRange.end) {
        meetsDateRange = meetsDateRange && itemDate <= new Date(filters.dateRange.end);
      }
      
      return meetsLoadTime && meetsBounceRate && meetsPageViews && meetsDateRange;
    });
  }, [data, filters]);

  const metrics = calculateAggregates(filteredData);

  const handleExportData = () => {
    if (!isAuthenticated) {
      addNotification({
        type: 'warning',
        title: 'Connexion requise',
        message: 'Veuillez vous connecter pour exporter les données.',
        action: {
          label: 'Se connecter',
          onClick: () => setIsAuthOpen(true)
        }
      });
      return;
    }

    exportToCSV(filteredData, 'ecommerce-analytics');
    addNotification({
      type: 'success',
      title: 'Export réussi',
      message: 'Les données ont été exportées en CSV.',
    });
  };

  const handleApplyFilters = (newFilters: typeof filters) => {
    setFilters(newFilters);
    addNotification({
      type: 'success',
      title: 'Filtres appliqués',
      message: 'Les filtres ont été mis à jour avec succès.',
    });
  };

  if (error) {
    return (
      <DashboardLayout>
        <div className="text-red-600 text-center py-8 bg-red-50 dark:bg-red-900/20 rounded-2xl mx-4">
          Erreur: {error}
        </div>
      </DashboardLayout>
    );
  }

  return (
    <DashboardLayout>
      <Header 
        timeframe={timeframe} 
        onTimeframeChange={setTimeframe}
        onExportData={handleExportData}
        onOpenFilters={() => setIsFiltersOpen(true)}
        onOpenAuth={() => setIsAuthOpen(true)}
        onOpenNotifications={() => setIsNotificationsOpen(true)}
      />
      
      {/* Grid des Métriques */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        <MetricCard
          title="Load Time Median"
          value={metrics ? `${metrics.loadTime}s` : '--'}
          trend="positive"
          change="-0.12s"
          icon={<Clock className="w-6 h-6 text-blue-600 dark:text-blue-400" />}
        />
        <MetricCard
          title="Bounce Rate"
          value={metrics ? `${metrics.bounceRate}%` : '--'}
          trend="negative"
          change="+1.2%"
          icon={<TrendingUp className="w-6 h-6 text-red-600 dark:text-red-400" />}
        />
        <MetricCard
          title="Page Views"
          value={metrics ? `${metrics.pageViews}M` : '--'}
          trend="positive"
          change="+12.4%"
          icon={<Users className="w-6 h-6 text-green-600 dark:text-green-400" />}
        />
        <MetricCard
          title="Pages Value"
          value={metrics ? `${metrics.pagesValue}%` : '--'}
          trend="positive"
          change="+0.2%"
          icon={<DollarSign className="w-6 h-6 text-purple-600 dark:text-purple-400" />}
        />
      </div>

      {/* Indicateur de Filtres Actifs */}
      {(filters.loadTime.min > 0 || filters.loadTime.max < 10 || 
        filters.bounceRate.min > 0 || filters.bounceRate.max < 100 ||
        filters.dateRange.start || filters.dateRange.end) && (
        <div className="mb-6 p-4 bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800 rounded-xl">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-2">
              <span className="text-sm font-medium text-blue-800 dark:text-blue-300">
                Filtres actifs
              </span>
              {filters.loadTime.min > 0 && (
                <span className="text-xs bg-blue-100 dark:bg-blue-800 text-blue-800 dark:text-blue-200 px-2 py-1 rounded">
                  Load Time ≥ {filters.loadTime.min}s
                </span>
              )}
              {filters.bounceRate.max < 100 && (
                <span className="text-xs bg-blue-100 dark:bg-blue-800 text-blue-800 dark:text-blue-200 px-2 py-1 rounded">
                  Bounce Rate ≤ {filters.bounceRate.max}%
                </span>
              )}
            </div>
            <button
              onClick={() => setFilters({
                loadTime: { min: 0, max: 10 },
                bounceRate: { min: 0, max: 100 },
                pageViews: { min: 0, max: 1000000 },
                dateRange: { start: '', end: '' }
              })}
              className="text-sm text-blue-600 dark:text-blue-400 hover:text-blue-800 dark:hover:text-blue-300"
            >
              Tout effacer
            </button>
          </div>
        </div>
      )}

      {/* Section Graphiques */}
      <div className="grid grid-cols-1 xl:grid-cols-2 gap-6 mb-8">
        <LoadTimeChart data={filteredData} timeframe={timeframe} isLoading={isLoading} />
        <PageViewsChart data={filteredData} timeframe={timeframe} isLoading={isLoading} />
      </div>

      {/* Scatter Chart */}
      <div className="mb-8">
        <PerformanceScatterChart data={filteredData} timeframe={timeframe} isLoading={isLoading} />
      </div>

      {/* Tableau de Performance */}
      <div className="mb-8">
        <PerformanceTable data={filteredData} isLoading={isLoading} />
      </div>

      {/* Modals et Panels */}
      <AdvancedFilters
        isOpen={isFiltersOpen}
        onClose={() => setIsFiltersOpen(false)}
        onApplyFilters={handleApplyFilters}
        currentFilters={filters}
      />

      <AuthModal
        isOpen={isAuthOpen}
        onClose={() => setIsAuthOpen(false)}
      />

      <NotificationPanel
        isOpen={isNotificationsOpen}
        onClose={() => setIsNotificationsOpen(false)}
      />

      {/* Toasts de Notifications */}
      <div className="fixed top-4 right-4 z-50 space-y-2">
        {notifications.slice(0, 3).map(notification => (
          <NotificationToast
            key={notification.id}
            notification={notification}
            onClose={removeNotification}
          />
        ))}
      </div>

      {/* Indicateur de Chargement Global */}
      {isLoading && (
        <div className="fixed top-4 left-1/2 transform -translate-x-1/2 bg-blue-500 text-white px-4 py-2 rounded-lg shadow-lg backdrop-blur-sm z-40">
          <div className="flex items-center space-x-2">
            <div className="w-2 h-2 bg-white rounded-full animate-pulse"></div>
            <span>Chargement des données...</span>
          </div>
        </div>
      )}
    </DashboardLayout>
  );
};

// App principal avec tous les providers
function App() {
  return (
    <ThemeProvider>
      <AuthProvider>
        <NotificationProvider>
          <AppContent />
        </NotificationProvider>
      </AuthProvider>
    </ThemeProvider>
  );
}

export default App;