import React from 'react';
import type { MetricCardProps } from '../../types';
import { TrendingUp, TrendingDown, Minus } from 'lucide-react';

export const MetricCard: React.FC<MetricCardProps> = ({ 
  title, 
  value, 
  trend, 
  change, 
  icon 
}) => {
  const getTrendConfig = () => {
    switch (trend) {
      case 'positive':
        return {
          color: 'text-green-600 bg-green-50 border-green-200',
          icon: <TrendingUp className="w-4 h-4" />
        };
      case 'negative':
        return {
          color: 'text-red-600 bg-red-50 border-red-200',
          icon: <TrendingDown className="w-4 h-4" />
        };
      default:
        return {
          color: 'text-gray-600 bg-gray-50 border-gray-200',
          icon: <Minus className="w-4 h-4" />
        };
    }
  };

  const trendConfig = getTrendConfig();

  return (
    <div className="bg-white/80 backdrop-blur-sm rounded-2xl shadow-sm border border-white/20 p-6 hover:shadow-md transition-all duration-300 hover:scale-105">
      <div className="flex items-center justify-between">
        <div className="flex-1">
          <p className="text-sm font-medium text-gray-600 mb-1">{title}</p>
          <p className="text-2xl font-bold text-gray-900">{value}</p>
        </div>
        {icon && (
          <div className="p-3 bg-blue-50 rounded-xl">
            {icon}
          </div>
        )}
      </div>
      
      {change && (
        <div className={`mt-3 inline-flex items-center px-3 py-1 rounded-full text-xs font-medium border ${trendConfig.color}`}>
          <span className="mr-1">{trendConfig.icon}</span>
          {change}
        </div>
      )}
    </div>
  );
};