import type { MetricData } from '../types';

export const generateMockData = (timeframe: '7d' | '30d' | '90d'): MetricData[] => {
  const days = timeframe === '7d' ? 7 : timeframe === '30d' ? 30 : 90;
  const data: MetricData[] = [];

  for (let i = 0; i < days; i++) {
    const date = new Date();
    date.setDate(date.getDate() - i);
    
    data.push({
      timestamp: date.toISOString().split('T')[0],
      loadTime: 1.5 + Math.random() * 1.5, // 1.5s - 3s
      bounceRate: 5 + Math.random() * 10, // 5% - 15%
      pageViews: Math.floor(100000 + Math.random() * 400000), // 100k - 500k
      startRender: 0.8 + Math.random() * 1, // 0.8s - 1.8s
      pagesValue: 0.3 + Math.random() * 0.7, // 0.3% - 1%
    });
  }

  return data.reverse();
};

export const timeframes = [
  { label: '7 derniers jours', value: '7d' as const },
  { label: '30 derniers jours', value: '30d' as const },
  { label: '90 derniers jours', value: '90d' as const },
];